.employees-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    max-width: 100%;
    height: 100%;
    overflow: auto;
  }
  .employees-table{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 75%;
    overflow: hidden;
    max-width: 1280px;
    border-collapse: collapse; /* Ensure borders collapse properly */

  }

  .employees-table thead {
    display: table; /* Ensure thead stays at the top */
    table-layout: fixed;
    position: sticky; /* Make thead sticky */
    background-color: rgb(var(--primary-color));
    width: 100%;
    top: 0; /* Stick to the top */
    z-index: 1; /* Ensure it stays above the content */
  }

  .employees-table tbody {
    display: block; /* Allow tbody to scroll independently */
    max-height: 400px; /* Adjust as needed */
    width: 100%;
  }

  .employees-table  table::-webkit-scrollbar{
    display: auto;
    width: 7.5px;
    height: 7.5px;
    margin: 10px;
  }
  
  .employees-table  table::-webkit-scrollbar-thumb{
    border-radius: 2rem;
    background-color: rgb(var(--second-color));
  }
  
  .employees-table  table::-webkit-scrollbar-track{
    background-color:transparent;
  }
  .employees-table  table::-webkit-scrollbar-corner {
    background: transparent;
}

.employees-table  table {
    min-height: 500px; 
    width: 100%;
    height: 100%;
    border-collapse: collapse;
    table-layout: fixed; /* make cells have equal width */
    max-width: 1250px;
    display: block;
    overflow-y: scroll;
  }

  .employees-table .content-container{
    max-height: 400px;
    width: 100%;

  }
  
.employees-table th, .employees-table td {
    padding: 8px;
    min-width: 65px;
    width: 100%;
    height: 50px;
    text-align: left;
    border: 1px solid rgb(var(--text-color)); /* add border to cells */
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    text-overflow: ellipsis;

  }
  .employees-table tr {
    display: flex;
    width: 100%;
    align-items: center;
    justify-content: space-between;
  }

 .employees-table th{
  color: rgb(var(--second-color));
  font-size: 20px;
  }

  .employees-table-top-content{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    text-shadow: -1px 1px 1px rgb(var(--text-color));
    color: rgb(var(--second-color));
    width: 75%;
    height: 185px;
    max-width: 1280px;
    margin-top: 6%;
  }
  .employees-table-top-content th{
    border: none;
    min-width: 220px ;
    height: 120px;
  }

  .employees-table-top-content th h1{
    margin: 0;
  }


  .employees-table-top-content .title{
    display: flex;
    justify-content: flex-start;
    font-size: 25px;
    flex-direction: column;
    align-items: flex-start;
    margin-bottom: 10px;
  }
  .employees-table-edit{
    display: flex;
    align-items: flex-start;
  }
  .employees-table-edit button{
    padding: 11px 11px;
  }
 

  .employees-table-top-content .actions{
    display: flex;
    justify-content: space-between;
    margin-bottom: 5px;
    width: 30%;
    margin-right: 10%;
  }

  .employees-table-header{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    width: 90%;
    text-shadow: -1px 1px 1px rgb(var(--text-color));
  }

  .employees-table-sort{
    cursor: pointer;
    width: 25px;
    height: 25px;
    fill: transparent;
    stroke: rgb(var(--second-color));
    transition: all 0.3s ease-in-out;
    filter: drop-shadow(-1px 1px 1px rgb(var(--text-color)))
  }

  .employees-table-sort.clicked {
    fill: rgb(var(--second-color));
    transition: all 0.3s ease-in-out;
  }

  .employees-table-content{
    display: flex;
    align-items: center;
    color: rgb(var(--text-color));
    width: 90%;
  }
  .employees-table-content-val{
    overflow: hidden;
    text-overflow: ellipsis;
    width: 100%;
  }
  .employees-table-content-val input{
    width: 92%;
    border-radius: 5px;
    text-overflow: ellipsis;
    height: 100%;
    background-color: transparent;
    color: rgb(var(--text-color));
    border: none;
    text-align: left;
    transition: all 0.3s ease-in-out;
  }
  .employees-table-content-val-edit input{
    width: 90%;
    border-radius: 5px;
    text-overflow: ellipsis;
    height: 100%;
    background-color: rgb(var(--second-color));
    color: rgb(var(--text-color));
    border: none;
    text-align: left;
    transition: all 0.3s ease-in-out;
  }
  
  
  .employees-table-top-content .actions .add-user{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    
  }
  .employees-table-top-content .actions .add-user{
    cursor: pointer;
  
    fill: none;
    color: rgb(var(--second-color));
    transition: color 0.3s ease-in-out,
                transform 0.1s ease-in-out,
                filter 0.1s ease-in-out;
  }
  .employees-table-top-content .actions .add-user .icon{
    cursor: pointer;
    height: 75px;
    width: 75px;
    stroke: rgb(var(--second-color));
    stroke-width: 1.5;
    fill: none;
    transition: stroke 0.3s ease-in-out,
                transform 0.1s ease-in-out,
                filter 0.1s ease-in-out;
    filter: drop-shadow(-2px 2px 1px  rgba(var(--text-color), 0.5));

  }
  
  .employees-table-delete-active{
    opacity: 1;
    max-width: 0;
    min-width: 8% !important;
    transition: all 0.3s ease-in-out;
  }
  .employees-table-delete{
    opacity: 0;
    max-width: 0;
    min-width: 8% !important;
    transition: all 0.3s ease-in-out;
  }


 
  .employees-table-top-content .actions .add-user:hover {
    color: rgb(var(--third-color));
    filter: drop-shadow(-2px 2px 1px  rgba(var(--text-color), 0.5));
  }
  
  .employees-table-top-content .actions .add-user:hover .icon {
    stroke: rgb(var(--third-color));
  }

  .employees-table-top-content .actions .add-user .icon:active,
  .employees-table-top-content .actions .add-user:active{
    transform: translate(-2px, 2px);
    filter: none;
  }

  .employees-table-top-content .actions .row-selector{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
  }
  .employees-table-top-content .actions .row-selector select{
    height: 75px;
    background-color: rgb(var(--primary-color));
    border: none;
    font-size: 25px;
    color: rgb(var(--text-color));
    outline: none;
    cursor: pointer;
    text-shadow: rgba(var(--text-color), 0.5);
  }


  .employees-table-actions{
    display: flex;
    width: 100%;
  }
  .employees-table-actions button{
    padding: 4% 10%;
    margin: 3%;
  }
  @media screen and (max-width: 860px){
    .employees-table-actions {
      display: flex;
      width: 100%;
      flex-direction: column;
      align-items: flex-start;
      height: 90%;
  }
  }


  @media screen and (max-width: 700px){
    .employees-table th, .employees-table td {
      padding: 5px;
    }
    .employees-table-top-content .title{
      font-size: 15px;
      width: 50%;
    }
    .employees-table{
      width: 90%;
    }
    .employees-table-top-content{
      margin-left: -5px;
    }
    .employees-table-header{
      font-size: 14px;
    }
    .employees-table-top-content th {
      min-width: 140px;
    }
    .employees-table-top-content .actions .add-user,
    .employees-table-top-content .actions .row-selector {
      font-size: 14px;
    }
    .employees-table-top-content .actions .row-selector select{
      font-size: 25px;
      margin: 8px;
    }
    .employees-table-top-content .actions .add-user .icon,
    .employees-table-top-content .actions .row-selector {
      height: 45px;
      width: 45px;
    }
    .employees-table-content-val{
      font-size: 11px;
    }
    .employees-table-top-content .actions{
      width: 42%;
      margin: 0;
    }
    .employees-table-edit button {
      padding: 8px 3px;
  }
  }